.card {
  border: 1px solid #eee;
  color: #aaa;
  padding: 1rem;
  margin: 1rem;
  background-color: #fff;
  height: 8rem;
  overflow: hidden;
  -webkit-box-shadow: 3px 3px 5px -1px rgba(66, 76, 97, 0.57);
  -moz-box-shadow: 3px 3px 5px -1px rgba(66, 76, 97, 0.57);
  box-shadow: 3px 3px 5px -1px rgba(66, 76, 97, 0.57);

  &.active {
    background-color: #f1f1f1;
    border: 1px solid #eaeaea;
    position: relative;
    top: -2px;
    left: -2px;
  }

  h3 {
    color: #777;
    font-size: 1.25rem;
  }

  h4 {
    font-size: 0.75rem;
    font-style: italic;
  }

  span {
    display: none;
  }
}

.update-note {
  display: none;
}

.delete-note {
  display: none;
}

.cards {
  height: 30rem;
  overflow: auto;
}

.content {
  height: 5rem;
  font-size: 2rem;
  padding: 0.75rem;
  margin-bottom: 1rem;
  border: 1px solid #eee;
  outline: none;
}

.new-task {
    outline: none;
}

.new-task-category-title {
    height: 3rem;
    font-size: 1.25rem;
    padding: 0.5rem;
    margin-bottom: 1rem;
    border: 1px solid #eee;
    outline: none;
}

.task-categories {
    list-style: none;
}

.task-category {
    margin-bottom: 0.25rem;
    padding: 0.5rem;
    padding-left: 1rem;
    background-color: #fff;
    border: 1px solid #eee;
    border-bottom-right-radius: 7px;

    &.active {
        background-color: #f1f1f1;
    }
}

.task {
    border: 1px solid #efefef;
    border-radius: 5px;
    width: 100%;
    height: 3rem;
    &.ghost {
        background-color: #f3f3f3;
        color: #aaa;
    }
    div {
        line-height: 3rem;
        padding-left: 1rem;
    }
    button.complete-task {
        float: left;
        height: 3rem;
        width: 3rem;
        outline: none;
    }
    button.save-task {
        float: right;
        margin-top: 0.2rem;
        display: none;
    }
    span {
        display: none;
    }
}

.body {
  border: 1px solid #eee;
  height: 30rem;
  outline: none;
  overflow: auto;
  margin-bottom: 1rem;
  padding: 1rem;
  white-space: pre-wrap;
}

.github-label {
  display: block;
  float: left;
  font-size: 0.75rem;
  padding: 0.25rem 0.5rem;
  border-radius: 2px;
  margin: 0 0.25rem;
}

.journal-body {
  font-size: 1.5rem;
  margin: 1rem 0;
  white-space: pre-wrap;
}

.journal-meta {
  margin-top: 3rem;
  color: #444;
  ul {
    list-style: none;
  }
  text-align: right;
}

.title {
  font-size: 2rem;
  min-height: 2rem;
  border: 1px solid #eee;
  outline: none;
  margin-bottom: 1rem;
  padding: 0.5rem;
}
